const calc = (size, material, options, promocode, result, calcState) => {
    const sizeBlock = document.querySelector(size),
          materialBlock = document.querySelector(material),
          optionsBlock = document.querySelector(options),
          promocodeBlock = document.querySelector(promocode),
          resultBlock = document.querySelector(result);

    let sum = 0;
        
    const calcFunc = () => {
        sum = Math.round((+sizeBlock.value) * (+materialBlock.value) + (+optionsBlock.value));

        if (sizeBlock.value == '' || materialBlock.value == '') {
            resultBlock.textContent = "Пожалуйста выберите размер и материал картины";
        } else if (promocodeBlock.value === 'IWANTPOPART') {
            resultBlock.textContent = Math.round(sum * 0.7);

            calcStateCange();

        } else {
            resultBlock.textContent = sum;
            
            calcStateCange();
        }
    };

    function calcStateCange() {
        calcState.sizeBlock = sizeBlock.value;
        calcState.materialBlock = materialBlock.value;
        calcState.optionsBlock = optionsBlock.value;
        calcState.sum = sum;

        if (promocodeBlock.value === 'IWANTPOPART'){
            calcState.promocodeBlock = true;
        } else {
            calcState.promocodeBlock = false;
        }

        console.log(calcState);
    }

    sizeBlock.addEventListener('change', calcFunc);
    materialBlock.addEventListener('change', calcFunc);
    optionsBlock.addEventListener('change', calcFunc);
    promocodeBlock.addEventListener('input', calcFunc);

};


export default calc;
